import PageTitle from "../PageTitle/PageTitle";


const Onas = () => {
  return (
    <div>
      <PageTitle>O nas</PageTitle>
      <p>Biuro nieruchomoci SAKA" jest firm zapewniajc kompleksow obsug 
      klienta w zakresie obrotu nieruchomociami. Specjalizujemy si w transakcjach 
      wynajmu-najmu oraz kupna-sprzeday nieruchomoci mieszkalnych, ale dziaamy rwnie 
      na rynku nieruchomoci komercyjnych. Prowadzimy dziaalno na terenie caego wojewdztwa 
      lskiego, a w szczeglnoci w okrgu katowickim i jego okolicach.</p>
    </div>
  )
}

export default Onas;